import styles from "./welcome_section.module.scss";
import welcomeSectionImage from "../../../../assets/image/encourages/welcome_section_image.png";
import { Section } from "../../../../components";

export const WelcomeSection = () => {
  return (
    <Section imageSide="right" imageSrc={welcomeSectionImage}>
      <div className={styles.section__description}>
        <h3 className={styles.description__title}>
          W trosce o potrzeby Twojego zwierzaka
        </h3>
        <p className={styles.description__text}>
          Kompleksowa wyszukiwarka usug dla zwierzt, ktra powstaa z myl o
          czworonogach i ich wacicielach, uatwiajc im dostp do szerokiego
          grona specjalistw.
        </p>
      </div>
    </Section>
  );
};
