import { useState } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import clsx from "clsx";
import { Button, Input, ErrorBox, PageTitle } from "../../../../../components";
import styles from "../form.module.scss";
import { DUMMY_LOGINS } from "../../../../../mock/auth";
import { clearFormAfterSubmit } from "../../../../../utils/clearFormAfterSubmit";

export const RegisterForm = () => {
  const schema = yup.object().shape({
    email: yup
      .string()
      .required("Podaj adres e-mail")
      .email("Podaj poprawny email")
      .max(25, "Maksymalnie 25 znakw"),
    username: yup
      .string()
      .required("Podaj nazw uytkownika")
      .min(3, "Minimum 3 znaki")
      .max(15, "Maksymalnie 15 znakw"),
    city: yup.string().required("Podaj miasto"),
    phone: yup
      .string()
      .required("Podaj numer telefonu")
      .matches(/^[0-9]+$/, "Numer musi posiada tylko cyfry")
      .min(7, "Numer musi posiada minimum 7 znakw")
      .max(10, "Numer musi posiada maksymalnie 10 znakw"),
    password: yup
      .string()
      .required("Podaj haso")
      .min(4, "Minimum 4 znaki")
      .max(15, "Maksymalnie 15 znakw"),
    confirmPassword: yup
      .string()
      .oneOf([yup.ref("password"), null], "Hasa musz by identyczne")
      .required("Hasa musz by identyczne"),
    business: yup.string(),
    NIP: yup.number().positive().integer(),
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(schema),
  });

  const [isPrivateMode, setIsPrivateMode] = useState(true);

  const changeTypeOfRegister = () => {
    setIsPrivateMode((prevState) => !prevState);
  };

  const formSubmit = (data) => {
    const { city, email, password, phone, username, business, NIP } = data;
    DUMMY_LOGINS.push({
      city,
      email,
      password,
      phone,
      username,
      business,
      NIP,
    });
    clearFormAfterSubmit(getValues, setValue);
  };

  return (
    <>
      <PageTitle>Rejestracja</PageTitle>
      <form
        onSubmit={handleSubmit(formSubmit)}
        className={clsx([styles.form, styles.form__auth])}
      >
        <Input
          {...register("email")}
          placeholder="E-mail"
          label="E-mail"
          type="text"
          classes={styles.form__input}
        />
        <ErrorBox>{errors.email?.message}</ErrorBox>
        <Input
          {...register("username")}
          placeholder="Nazwa uytkownika"
          label="Nazwa uytkownika"
          type="text"
          classes={styles.form__input}
        />
        <ErrorBox>{errors.username?.message}</ErrorBox>
        <Input
          {...register("city")}
          placeholder="Miasto"
          label="Miasto"
          type="text"
          classes={styles.form__input}
        />
        <ErrorBox>{errors.city?.message}</ErrorBox>
        <Input
          {...register("phone")}
          placeholder="Numer telefonu"
          label="Numer telefonu"
          type="string"
          classes={styles.form__input}
        />
        <ErrorBox>{errors.phone?.message}</ErrorBox>
        <Input
          {...register("password")}
          type="password"
          placeholder="Haso"
          label="Haso"
          classes={styles.form__input}
        />
        <ErrorBox>{errors.password?.message}</ErrorBox>
        <Input
          {...register("confirmPassword")}
          type="password"
          placeholder="Powtrz haso"
          label="Powtrz haso"
          classes={styles.form__input}
        />
        <ErrorBox>{errors.confirmPassword?.message}</ErrorBox>
        {!isPrivateMode && (
          <>
            <Input
              {...register("business")}
              placeholder="Nazwa firmy"
              label="Nazwa firmy"
              type="text"
              classes={styles.form__input}
            />
            <ErrorBox>{errors.business?.message}</ErrorBox>
            <Input
              {...register("NIP")}
              placeholder="NIP"
              label="NIP"
              type="text"
              classes={styles.form__input}
            />
            <ErrorBox>{errors.NIP?.message}</ErrorBox>
          </>
        )}
        <div className={styles.form__separator} />
        <Button type="submit" onClick={() => {}}>
          Zarejestruj
        </Button>
        <Button type="button" onClick={changeTypeOfRegister}>
          Zarejestruj jako {!isPrivateMode ? "osoba prywatna" : "firma"}
        </Button>
      </form>
    </>
  );
};
